@import url('https://fonts.googleapis.com/css2?family=Hanalei&display=swap');

 * {
	margin: 0;
	padding: 0;
	border: 0;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

/* General css */

.loader {
    margin: 10em 0;
    border: 16px solid #f3f3f3;
    border-radius: 50%;
    border-top: 16px solid blue;
    border-right: 16px solid green;
    border-bottom: 16px solid red;
    border-left: 16px solid pink;
    width: 120px;
    height: 120px;
    -webkit-animation: spin 2s linear infinite;
    animation: spin 2s linear infinite;
  }
  
  @-webkit-keyframes spin {
    0% { -webkit-transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); }
  }
  
  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }

body, html {
    background-color: #f6f6f6;
}

/* Login page css */

.login-container {
    margin-top: 10%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.login-input {
    padding: 10px;
    border-radius: 10px;
    margin: 10px 0px;
    border: 0.5px solid black;
    width: 90%;
}

.login-btn {
    cursor: pointer;
    border: 1px solid;
    border-radius: 10px;
    padding: 10px;
}

.landing-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100%;
}

.blog-card {
    width: 60%;
    display: flex;
    flex-direction: row;
    border: 1px solid #000000;
    background-color: #ffffff;
    padding: 20px;
    margin-top: 25px;
    border-radius: 10px;
    -webkit-transition-property: width;
    transition-property: width;
    -webkit-transition-duration: 0.3s;
    transition-duration: 0.3s;
    -webkit-transition-timing-function: ease-out;
    transition-timing-function: ease-out;
    color: inherit;
    text-decoration: none;
}

.blog-card:hover {
    width: 62%;
    box-shadow: 4px 6px 20px 0px #b8b8b8;
}

.card-title {
    flex: 1;
    margin: 0px 10px;
    font-size: calc(12px + 2vw);
    font-weight: 900;
}

/* react markdown css */

.editor-container {
    width: 75%;
    margin: 4rem auto;
}

/* admin space css */

.title-input {
    width: 98%;
    padding: 10px;
    font-size: initial;
    margin: 0.5rem 0rem 2rem 0rem;
    height: 2rem;
    border: 1px solid;
    border-radius: 10px;
}

.description-input {
    margin: 0.5rem 0rem 2rem 0rem;
    border: 1px solid;
    border-radius: 4px;
}

/* compoents css */

.header-btn {
    align-self: center;
    text-decoration: none;
    border: 2px solid black;
    color: black;
    font-size: 1.5rem;
    padding: 6px;
    border-radius: 10px;
    margin-right: 2rem;
}

/* detail page css */

.post-title {
    font-size: 4vw;
    text-align: center;
    margin: 60px 20px;
}

.post-description-container {
    width: 75%;
    margin-bottom: 5em;
}

.post-description-container p {
    font-size: 1.25rem;
    margin: 20px 0px;
}

.post-description-container h1 {
    font-size: 2.5rem;
}

.post-description-container h2 {
    font-size: 2rem;
}

.post-description-container h3 {
    font-size: 1.75rem;
}

.post-description-container pre {
    margin: 32px;
    font-size: 1rem;
    border: 1px solid;
    padding: 30px;
}

.post-description-container img {
    display: block;
    width: 65%;
    text-align: center;
    margin: 0 auto;
}


/* helper css */

.d-flex {
    display: flex;
}

.flex-justify-space-btw {
    justify-content: space-between;
}

.m-30 {
    margin: 30px;
}

.ml-30 {
    margin-left: 30px;
}

.w-25 {
    width: 25%;
}

.w-35 {
    width: 35%;
}

.w-50 {
    width: 50%;
}

.w-100 {
    width: 100%;
}